.f-header {
  height: 600px;
  -webkit-mask-image: -webkit-linear-gradient(left, rgba(0,0,0, 0.95) 39%, rgba(0,0,0, 0.0) 40%, rgba(0,0,0, 0.0) 45%, rgba(0,0,0, 1.0) 70%, rgba(0,0,0,0.0) 100%);
          mask-image: linear-gradient(to right, rgba(0,0,0, 0.95) 39%, rgba(0,0,0, 0.0) 40%, rgba(0,0,0, 0.0) 45%, rgba(0,0,0, 1.0) 70%, rgba(0,0,0,0.0) 100%);
