.nav {
  position: fixed;
  display: block;
  z-index: 9999;
}

.dropdown-menu {
	background-color: white;
}

.dropdown-menu li a{
	color: white;
}

/*.navbar-nav > li:hover { background: #eee; }*/

.navbar-nav > li {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
}

.navbar-nav > li:before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 0;
  right: 100%;
  bottom: 0;
  background: #2098D1;
  height: 4px;
  -webkit-transition-property: right;
  transition-property: right;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}

.navbar-nav > li:hover:before, .navbar-nav > li:focus:before, .navbar-nav > li:active:before {
  right: 0;
}

ul.navbar-nav li.dropdown:hover > ul.dropdown-menu {
    display: block;
    width: 100px; 
    margin: 0;
    padding: 10px;
    background-color: #2f3131;
   /* border-color: #426E86;
    border-width: 2px;
    border-style: solid;*/
}

.sub-header {
  background-image: -webkit-linear-gradient( #426E86 90%, #F9BA32 100%);
  background-image: linear-gradient( #426E86 90%, #F9BA32 100%)
}

/*Cogs*/

.spoke {
  background-color: grey;
  position: absolute;
  height: 175px;
  width: 30px;
  margin-left: -15px;
  margin-top: -1px;
  left: 50%;
  border-radius: 5px;
  z-index: 10;
}

.spoke + .spoke {
  -webkit-transform: rotateZ(30deg);
          transform: rotateZ(30deg);
}

.spoke + .spoke + .spoke {
  -webkit-transform: rotateZ(60deg);
          transform: rotateZ(60deg);
}

.spoke + .spoke + .spoke +.spoke {
  -webkit-transform: rotateZ(90deg);
          transform: rotateZ(90deg);
}

.spoke + .spoke + .spoke + .spoke + .spoke {
  -webkit-transform: rotateZ(120deg);
          transform: rotateZ(120deg);
}

.spoke + .spoke + .spoke + .spoke + .spoke + .spoke {
  -webkit-transform: rotateZ(150deg);
          transform: rotateZ(150deg);
}

.cog {
  position:relative;
  margin: 30px;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  z-index: 10;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-name: mymove;
          animation-name: mymove;
}

