/*SlideShow*/


.slide {
  height: 100%;
  width: 100%;
}

.slider {
  position: relative;
  margin: 0 auto;
  overflow: hidden;
  white-space: nowrap;
  width: 500px;
  height: 300px;
}

.slider-wrapper {
  position: relative;
  height: 100%;
  width: 100%;
}

.slide {
  display: inline-block;
  height: 100%;
  width: 100%;
}

.arrow {
  height: 50px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f9f9f9;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transition: -webkit-transform ease-in .1s;
  transition: -webkit-transform ease-in .1s;
  transition: transform ease-in .1s;
  transition: transform ease-in .1s, -webkit-transform ease-in .1s;
}

.nextArrow {
  position: absolute;
  top: 50%;
  right: 0px;
  z-index: 999;
  color: #fff;
}

.backArrow {
  position: absolute;
  top: 50%;
  left: 0px;
  z-index: 999;
  color: #fff;
}

.fa-arrow-right:before, .fa-arrow-left:before {
  color: #222
}

